import { Camera, CameraOptions } from '@ionic-native/camera';
import { AngularFireAuth } from 'angularfire2';
import { AngularFire, FirebaseAuthState } from 'angularfire2';
import { Injectable } from '@angular/core';
import { Http } from '@angular/http';
import 'rxjs/add/operator/map';

@Injectable()
export class ImageService {

  private cameraOptions: CameraOptions = {
      quality: 100,
      destinationType: this.camera.DestinationType.DATA_URL,
      encodingType: this.camera.EncodingType.JPEG,
      mediaType: this.camera.MediaType.PICTURE
    };

  private images = [];
    
  constructor(public af: AngularFire,
              private afAuth: AngularFireAuth,
              private camera: Camera) {
  }

  takePicture(petUuid: string) {
    this.camera.getPicture(this.cameraOptions)
      .then(data => {
        let base64Image = 'data:image/jpeg;base64,' + data;
        return this.uploadImage(base64Image, petUuid);
      })
      .then(data => {
        this.images.push(data.a.name);
        localStorage.setItem('images', JSON.stringify(this.images));
      });
  }

  uploadImage(image: string, petId: string): any {
    let storageRef = firebase.storage().ref();
    let imageName = this.generateUUID();
    let imageRef = storageRef.child(`${petId}/${imageName}.jpg`);
    return imageRef.putString(image, 'data_url');
  }

  //getImage(petId: string, imageId: string): any {
  //      let storageRef = firebase.storage().ref();
  //      let imageRef = storageRef.child(`${petId}/${imageId}`);
  //      return imageRef.getDownloadURL();
  //}

  private generateUUID(): string {
    function s4() {
      return Math.floor((1 + Math.random()) * 0x10000)
        .toString(16)
        .substring(1);
    }
    return s4() + s4() + '-' + s4() + '-' + s4() + '-' +
      s4() + '-' + s4() + s4() + s4();
  }

}



